import React from 'react';
import './UserBuyDropletPayment.scss';
import {Link} from 'react-router';

import Sidebar from '../../Sidebar/components/Sidebar.js';
import Header from '../../../Header/components/Header.js';
import PaymentCard from '../../../Widgets/PaymentCard/components/PaymentCard.js';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class UserBuyDropletPayment extends React.Component {
    constructor(props) {
        super(props);
    }

    goToIndex = () => {
        this.props.router.push('/');
    }

    goToLogin = () => {
        this.props.router.push('/login');
    }

    goToBuyComplete = () => {
      this.props.router.push('/buy-droplet');
    }

    render() {
        return (
          <div className="user-buy-droplet">
              <Header isLoggedIn={true} goHome={this.goToIndex} />

              <div className="container notification-container">
                  <div className="row">
                      <Sidebar />

                      <div className="col-10">
                          <div className="notification-section">
                              <div className="title-section">
                                  <h4 className="title-text">Buy Droplet - Payment Detail</h4>
                              </div>
                              <PaymentCard />
                          </div>
                          <button className="btn btn-green btn-save" onClick={this.goToBuyComplete}>Checkout Now </button>

                      </div>
                  </div>
              </div>
          </div>
      );
    }
}
export default UserBuyDropletPayment;



